<template name="mMap">
  <view class="page-body">
    <map
      class="m-map"
      id="map1"
      :latitude="latitude"
      :longitude="longitude"
      show-location="true"
      :markers="covers"
      @tap="handleMap"
      :style="this.isClickDow ? 'height: 45vh;': 'height: 60vh;'"
    >
      <cover-image class="headerBtn" src="../../static/images/posheader.png"></cover-image>
      <cover-view class="okBtn" @click="handleOkBtn">{{ $i18nMsg('lang-map-ok') }}</cover-view>
      <cover-view class="offBtn" @click="handleOffBtn">{{ $i18nMsg('lang-form-modalCancel') }}</cover-view>
      <cover-image class="controls-img" @click="handleCurrPos" src="../../static/images/position.png"></cover-image>
    </map>
    <view class="pointContent" :style="this.isClickDow ? 'height: 56vh;': 'height: 40vh;'">
      <view v-show="this.isClickDow" class="iconsty">
        <m-font-icon icon="APP_31" :iconSize="40" class="icon" @click="handleDow"></m-font-icon>
      </view>
      <input class="input" placeholder="搜索地点" v-model="searchName" @blur="getList" @confirm="handleConfirm" @focus="focusInp" confirm-type="search" hold-keyboard="true" adjust-position="false"/>
      <scroll-view scroll-y="true" style="height: 60vh" @scrolltolower="handleLower">
        <view class="poslist" v-for="(item, i) of sidePosition" :key="i" @click="handlechange(item, i)">
          <view class="font1">
            <view class="name">{{item.name}}</view>
            <m-font-icon class="icon" icon="iconxuanzhong" :iconSize="40" :style="{display: item.check ? 'inline-block' : 'none'}"></m-font-icon>
          </view>
          <view class="font2">{{item.distance}}m{{$i18nMsg('lang-map-inner')}} | {{item.city}} {{item.address}}</view>
        </view>
      </scroll-view> 
    </view>
  </view>
</template>
<script>
let mapSearch = weex.requireModule('mapSearch');
export default {
  data() {
    return {
      navMap: '', // 地图对象
      latitude: 0,
      longitude: 0,
      address: '',
      pageIndex: 1,
      searchName: '',
			beforeSearchName: '',
      isClickDow: false,
      elementId: '',
      covers: [ // 坐标
        {
          id: 1,
          latitude: 0,
          longitude: 0,
          iconPath: "../../static/images/location.png",
        }
      ],
      sidePosition: [] // 列表数组
    };
  },
  methods: {
    // 下拉的icon
    handleDow() {
			this.isClickDow = false
		},
    // 点击键盘搜索
    handleConfirm() {
			this.pageIndex = 1;
			this.sidePosition = [];
			uni.hideKeyboard()
    },
    // 获取焦点事件
    focusInp (e) {
      this.isClickDow = true;
    },
    handleLower(e) {
      this.pageIndex++;
      this.getList()
    },
    // 发送信息的页面
    // $emit(eventName, data)  
    handleOkBtn() {
      let data = `${this.address},${this.latitude},${this.longitude}`
      uni.$emit(this.elementId, {  
        data
      });
      uni.navigateBack();
    },
    // 取消返回上个页面
    handleOffBtn() {
      uni.navigateBack();
    },
    // 移动图标
    moveToLocation(val) {
      let nmap = this.navMap;
      nmap.moveToLocation({
        longitude: val.longitude,
        latitude: val.latitude,
      });
    },
    // 赋值date
    giveDate (val, address) {
      let _this = this;
      _this.latitude = val.latitude; //当前位置经度
      _this.longitude = val.longitude; //当前位置纬度
      _this.covers[0].latitude = val.latitude; //当前位置经度
      _this.covers[0].longitude = val.longitude; //当前位置纬度
      _this.address = address || _this.address; //当前位置纬度
    },
    // 点击列表
    handlechange(val, index) {
      this.sidePosition.forEach(item => item.check = false)
      this.sidePosition[index].check = true;
      let pos = val.location;
      this.giveDate(pos, val.name);
      this.moveToLocation(pos);
    },
    // 获取数组
    getList() {
      uni.hideKeyboard()
			console.log(this.searchName, this.beforeSearchName)
      if(this.searchName !== this.beforeSearchName) {
        this.sidePosition =[];
        this.pageIndex = 1;
      }
			this.beforeSearchName = this.searchName;
      let point = this.covers[0], _this = this;
      mapSearch.reverseGeocode({
        point: {
          latitude: point.latitude,
          longitude: point.longitude
        }
      }, function (res) {
        _this.address = res.address;
        mapSearch.poiSearchNearBy({
          point: {
            latitude: point.latitude,
            longitude: point.longitude
          },
          key: _this.searchName,
          radius: 9999999,
          index: _this.pageIndex
        }, res => {
					console.log(_this.searchName)
          console.log(res);
          res.poiList.forEach(val => {
            val.check = false;
            _this.sidePosition.push(val);
          })
        })
      })
    },
    // 移动地图
    handleMap() {
      let nmap = this.navMap, _this = this;
      _this.pageIndex = 1;
      _this.sidePosition = []
      nmap.getCenterLocation({
        success: function(res) {
          _this.giveDate(res);
          _this.moveToLocation(res);
          _this.getList();
        }
      })
    },
    // 初始化地图
    handleCurrPos() {
      let _this = this;
      _this.pageIndex = 1;
      _this.sidePosition = []
      uni.getLocation({
        type: "gcj02", //返回可以用于uni.openLocation的经纬度
        geocode: true,
        success: function (res) {
          _this.giveDate(res);
          _this.moveToLocation(res)
          _this.getList();
        },
        fail: function(res) {
          uni.showModal({
            title: '提示',
            content: JSON.stringify(res),
          });
        }
      });
    }
  },
  onLoad(options) {
    this.elementId = String(options.elementId);
    console.log(this.elementId, options,options, options.elementId)
  },
  mounted(options) {
    console.log(options)
    let nmap = uni.createMapContext("map1", this); // $getAppMap()
    this.navMap = nmap;
    this.handleCurrPos();
  },
};
</script>
